
import { Card, Col, Row} from 'antd'
import React, { useContext} from 'react'
import 'antd/dist/antd.css'
import myContext from './mycreate'
import {StarOutlined,BulbOutlined,UserOutlined,FileExclamationOutlined} from '@ant-design/icons';
const List=()=>{  
    const list=useContext(myContext) 
    
        if(list!==undefined){      
    return (             
            <div style={{width:"100%",textAlign:"center"}}>           
                <Row gutter={20} style={{margin:0,marginTop:"20px",display:'flex',justifyContent:'center'}}>
                    {
                    list.data.items.map((item,index)=><Col span={4} xs={24} sm={12} md={6} lg={4} xl={4} key={index}>
                    <Card size="small" title={'#'+(index+1)} 
                        cover={<img alt="example" src={item.owner.avatar_url} 
                        onClick={()=>{window.location.href = item.html_url;}}
                        />}
                    >         
                            <p href={item.html_url}>{item.full_name}</p>             
                            <p><UserOutlined style={{ color: 'orange' }}/> {item.owner.login}</p>
                            <p><StarOutlined style={{ color: 'yellow' }}/>{item.stargazers_count}</p>
                            <p><BulbOutlined style={{ color: 'lightblue' }}/>{item.forks}</p>
                            <p><FileExclamationOutlined style={{ color: 'purple' }}/>{item.open_issues}</p>
                        </Card>

                    </Col>)
                    }
                    </Row>
                </div>
    )}else{
        return(
            <h2 style={{textAlign:'center'}}>Loading....</h2>
            
        )
    }
}
                



  export default List